
.buyImg {
  width: 92%;
  max-height: 90%;
  overflow: hidden;
  background-color: white;
  opacity: 1;
  position: fixed;
  top: 5%;
  z-index: 10000;
  transition: all .3s ease;
  left: 4%;
  padding: 0;
  border-radius: 0.2rem;
  box-shadow: 1px 1px 1px #eee;
}
.pay-bg {
    opacity: 0.7;
    width: 100%;
    height: 100%;
    z-index: 9999;
    position: fixed;
    top: 0;
    background: #000;
}
.buyImg img{
  width: 100%
}



